@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
*{
  border-radius: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sub-window{margin:10px;display:flex;height:87.2vh;flex-direction:column;justify-content:center}.sub-window .search{min-width:350px;min-height:350px;max-height:800px;max-width:800px;display:flex;margin-left:auto;margin-right:auto;flex-direction:column;justify-content:center;width:60vw;height:60vw}

.card{box-sizing:border-box;width:100%;height:67%;border:1px solid black;margin:auto;overflow:hidden;position:relative;border-radius:10px}.card .card-top{height:38%;background-size:cover;background-repeat:no-repeat;background-origin:content-box;background-position:center;background-color:black}.card .card-top span{background-color:green}.card .bottom{display:flex;justify-content:center}.card .bottom .card-circle{display:flex;position:absolute;top:18%;margin:auto;background-color:red;border:1px solid black;height:40%;width:28%;border-radius:100%;overflow:hidden}.card .bottom .card-circle img{width:100%;height:100%}.card .bottom .card-bottom{margin-top:11%;text-align:center;width:100%;height:70%}.card .bottom .card-bottom h1{margin:4.5%}.card .bottom .card-bottom .results{margin-top:0%;font-size:2rem;font-weight:normal;width:100%}@media screen and (min-width: 720px){.card{max-height:80vh;max-width:48vw}.card .bottom .card-bottom .results{font-size:3em}}

.input{margin:auto;width:45vw;min-width:300px;max-width:400px;height:60px;overflow:hidden;position:relative;margin-top:20px;font-size:1em}.input .label-input{font-size:1.2em;height:100%;width:100%;border:none;box-sizing:border-box;padding-top:30px;outline:none;border-bottom:2px solid #4e4e4e}.input .label-name{position:absolute;pointer-events:none;left:0;bottom:0;width:100%;font-size:2em}.input .label-name::after{content:"";position:absolute;left:0;bottom:0;width:100%;height:100%;border-bottom:2px solid #6fa4d6;transform:translateX(-100%);transition:all 0.3s ease}.input .label-name .content-name{position:absolute;bottom:0;left:0;font-size:1em;transform:translateY(0);transition:all 0.4s ease}.input input[type="submit"]{font-size:2.3em;display:block;position:absolute;right:0.1vw;bottom:5%;background-color:inherit;border:none;outline:none;cursor:pointer}.input .label-input:focus+.label-name::after,.input .label-input:valid+.label-name::after{transform:translateX(0)}.input .label-input:focus+.label-name .content-name,.input .label-input:valid+.label-name .content-name{font-size:0.58em;transform:translateY(-150%);color:#6fa4d6}

.error-page{width:100%;height:90vh}.error-page .punchline{display:flex;height:90%;flex-direction:column;justify-content:center;margin:auto;text-align:center}.error-page .punchline #explanation{font-size:2rem;font-weight:normal}

*{margin:0;padding:0;box-sizing:border-box}nav{border:2px solid black}nav ul{list-style-type:none;color:white;background-color:black;height:100%;display:flex;justify-content:center}nav ul li{margin:0 5px;padding:15px}nav ul li a{font-size:1em;text-decoration:none;color:white}nav ul li:hover{color:black;background-color:white}nav ul li:hover a{color:black}

